import React, { Component } from 'react';
import {
  Form,
  Input,
  Button,
  Checkbox,
  Row,
  Col,
  Tabs,
  Dropdown,
  Menu,
  message,
} from 'antd';
import {
  UserOutlined,
  LockOutlined,
  MailTwoTone,
  MobileTwoTone,
  AlipayCircleOutlined,
  TaobaoCircleOutlined,
  WeiboCircleOutlined,
} from '@ant-design/icons';
import PropTypes from 'prop-types';
import getLogin from './service/index';
import logo from './images/logo.f0355d39.svg';
import './login.css';

const { TabPane } = Tabs;

const handleValidator = (rule, val, callback) => {
  if (!val) {
    callback();
  }
  const myReg = /^[1][3,4,5,7,8][0-9]{9}$/;
  const validateResult = myReg.test(val); // 自定义规则
  if (!validateResult) {
    callback('手机号格式错误！');
    return;
  }
  callback();
};

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nowIndex: '1',
      moverLang: false,
      visible: false,
      isSendMess: false,
      colStyle1: 16,
      colStyle2: 8,
      countDown: 60,
    };
  }

  render() {
    const menu = (
      <Menu onClick={(e) => this.handleMenuClick(e)}>
        <Menu.Item>
          <span>🇺🇸 English</span>
        </Menu.Item>
        <Menu.Item>
          <span>🇵🇱 Bahasa Indonesia</span>
        </Menu.Item>
        <Menu.Item>
          <span>🇧🇷 Português</span>
        </Menu.Item>
        <Menu.Item>
          <span>🇨🇳 简体中文</span>
        </Menu.Item>
        <Menu.Item>
          <span>🇭🇰 繁体中文</span>
        </Menu.Item>
      </Menu>
    );
    return (
      <div className="rootBox">
        <div className="header">
          <Dropdown
            overlay={menu}
            onVisibleChange={(e) => this.handleVisibleChange(e)}
            visible={this.state.visible}
          >
            <a className="antDropdownLink">
              <span>
                <svg
                  viewBox="0 0 24 24"
                  focusable="false"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path d="M0 0h24v24H0z" fill="none"></path>
                  <path
                    d="M12.87 15.07l-2.54-2.51.03-.03c1.74-1.94 2.98-4.17 3.71-6.53H17V4h-7V2H8v2H1v1.99h11.17C11.5 7.92 10.44 9.75 9 11.35 8.07 10.32 7.3 9.19 6.69 8h-2c.73 1.63 1.73 3.17 2.98 4.56l-5.09 5.02L4 19l5-5 3.11 3.11.76-2.04zM18.5 10h-2L12 22h2l1.12-3h4.75L21 22h2l-4.5-12zm-2.62 7l1.62-4.33L19.12 17h-3.24z "
                    className="css-c4d79v"
                  ></path>
                </svg>
              </span>
            </a>
          </Dropdown>
        </div>
        {/* 中部 */}
        <div className="bodyer">
          <div className="logoBox">
            <div className="logoChildBox">
              <img className="logo" src={logo} alt=""></img>
              <span className="logoName">Ant Design</span>
            </div>
          </div>
          <div className="bodyerText">
            Ant Design 是西湖区最具影响力的 Web 设计规范
          </div>
          <div className="bodyerForLogin">
            <div className="inputBox">
              <Form
                name="normalLogin"
                className="loginForm"
                initialValues={{
                  remember: true,
                }}
                onFinish={(e) => this.onFinish(e)}
              >
                <Tabs
                  defaultActiveKey="1"
                  onChange={(e) => this.getChangeType(e)}
                  centered
                >
                  <TabPane tab="账户密码登录" key="1" >
                  {this.state.nowIndex === '1' ? (<div>
                    <Form.Item
                      name="username"
                      style={{ textAlign: 'left' }}
                      rules={[
                        {
                          required: true,
                          message: '用户名不能为空!',
                        },
                      ]}
                    >
                      <Input
                        size="large"
                        prefix={<UserOutlined />}
                        placeholder="请输入用户名"
                      />
                    </Form.Item>
                    <Form.Item
                      name="password"
                      style={{ textAlign: 'left' }}
                      rules={[
                        {
                          required: true,
                          message: '密码不能为空!',
                        },
                      ]}
                    >
                      <Input
                        size="large"
                        prefix={<LockOutlined />}
                        type="password"
                        placeholder="请输入密码"
                      />
                    </Form.Item>
                    </div>) : (<div></div>)
                  }
                  </TabPane>
                  <TabPane tab="手机号登录" key="2" >
                  {this.state.nowIndex === '2' ? (<div>
                    <Form.Item
                      name="mobilePhone"
                      style={{ textAlign: 'left' }}
                      rules={[
                        {
                          required: true,
                          message: '请输入手机号！',
                        },
                        {
                          validator: handleValidator,
                          message: '手机号格式错误!',
                        },
                      ]}
                    >
                      <Input
                        size="large"
                        placeholder="手机号"
                        prefix={<MobileTwoTone className="inputIcon" />}
                      />
                    </Form.Item>
                    <Form.Item
                      name="message"
                      style={{ textAlign: 'left' }}
                      rules={[
                        {
                          required: true,
                          message: '请输入验证码！',
                        },
                      ]}
                    >
                      <Row>
                        <Col
                          span={this.state.colStyle1}
                          style={{ padding: '0 2px 0 0', float: 'left' }}
                        >
                          <Input
                            size="large"
                            placeholder="验证码"
                            prefix={<MailTwoTone className="inputIcon" />}
                          />
                        </Col>
                        <Col
                          span={this.state.colStyle2}
                          style={{ padding: '0 0 0 6px', float: 'right' }}
                        >
                          <Button
                            style={{ width: '100%' }}
                            disabled={this.state.isSendMess}
                            size="large"
                            onClick={() => this.getSixty()}
                          >
                            {!this.state.isSendMess
                              ? '点击获取'
                              : `${this.state.countDown}秒后重新获取`}
                          </Button>
                        </Col>
                      </Row>
                    </Form.Item>
                    </div>) : (<div></div>)
                  }
                  </TabPane>
                </Tabs>
                <Form.Item
                  className="loginFormRemember "
                  style={{ margenBottom: '20px' }}
                >
                  <Form.Item
                    name="remember"
                    valuePropName="checked"
                    style={{ float: 'left' }}
                  >
                    <Checkbox>自动登录</Checkbox>
                  </Form.Item>
                  <a
                    className="login-form-forgot"
                    href=""
                    style={{ float: 'right' }}
                  >
                    忘记密码？
                  </a>
                </Form.Item>

                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="loginFormButton"
                  >
                    提交
                  </Button>
                </Form.Item>
              </Form>
              <div className="otherLoginFunc">
                其他登录方式
                <AlipayCircleOutlined />
                <TaobaoCircleOutlined />
                <WeiboCircleOutlined />
              </div>
            </div>
          </div>
        </div>
        {/* 底部 */}
        <div className="footer">
          <div className="textBox">
            <div className="links">
              <a
                title="Ant Design Pro"
                href="https://pro.ant.design"
                style={{ marginRight: '40px', color: 'rgba(0, 0, 0, 0.45)' }}
              >
                Ant Design Pro
              </a>
              <a
                title="github"
                style={{ marginRight: '40px', color: 'rgba(0, 0, 0, 0.45)' }}
                href="https://github.com/ant-design/ant-design-pro"
              >
                <span
                  role="img"
                  aria-label="github"
                  className="anticon anticon-github"
                >
                  <svg
                    viewBox="64 64 896 896"
                    focusable="false"
                    data-icon="github"
                    width="1em"
                    height="1em"
                    fill="currentColor"
                    aria-hidden="true"
                  >
                    <path d="M511.6 76.3C264.3 76.2 64 276.4 64 523.5 64 718.9 189.3 885 363.8 946c23.5 5.9 19.9-10.8 19.9-22.2v-77.5c-135.7 15.9-141.2-73.9-150.3-88.9C215 726 171.5 718 184.5 703c30.9-15.9 62.4 4 98.9 57.9 26.4 39.1 77.9 32.5 104 26 5.7-23.5 17.9-44.5 34.7-60.8-140.6-25.2-199.2-111-199.2-213 0-49.5 16.3-95 48.3-131.7-20.4-60.5 1.9-112.3 4.9-120 58.1-5.2 118.5 41.6 123.2 45.3 33-8.9 70.7-13.6 112.9-13.6 42.4 0 80.2 4.9 113.5 13.9 11.3-8.6 67.3-48.8 121.3-43.9 2.9 7.7 24.7 58.3 5.5 118 32.4 36.8 48.9 82.7 48.9 132.3 0 102.2-59 188.1-200 212.9a127.5 127.5 0 0138.1 91v112.5c.8 9 0 17.9 15 17.9 177.1-59.7 304.6-227 304.6-424.1 0-247.2-200.4-447.3-447.5-447.3z"></path>
                  </svg>
                </span>
              </a>
              <a
                title="Ant Design"
                style={{ color: 'rgba(0, 0, 0, 0.45)' }}
                href="https://ant.design"
              >
                Ant Design
              </a>
            </div>
            <div className="texts">
              Copyright
              <span
                role="img"
                aria-label="copyright"
                className="anticon anticon-copyright"
              >
                <svg
                  viewBox="64 64 896 896"
                  focusable="false"
                  data-icon="copyright"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  aria-hidden="true"
                >
                  <path d="M512 64C264.6 64 64 264.6 64 512s200.6 448 448 448 448-200.6 448-448S759.4 64 512 64zm0 820c-205.4 0-372-166.6-372-372s166.6-372 372-372 372 166.6 372 372-166.6 372-372 372zm5.6-532.7c53 0 89 33.8 93 83.4.3 4.2 3.8 7.4 8 7.4h56.7c2.6 0 4.7-2.1 4.7-4.7 0-86.7-68.4-147.4-162.7-147.4C407.4 290 344 364.2 344 486.8v52.3C344 660.8 407.4 734 517.3 734c94 0 162.7-58.8 162.7-141.4 0-2.6-2.1-4.7-4.7-4.7h-56.8c-4.2 0-7.6 3.2-8 7.3-4.2 46.1-40.1 77.8-93 77.8-65.3 0-102.1-47.9-102.1-133.6v-52.6c.1-87 37-135.5 102.2-135.5z"></path>
                </svg>
              </span>
              2019 蚂蚁金服体验技术部出品
            </div>
          </div>
        </div>
      </div>
    );
  }

  onFinish(values) {
    const data = {
      userName: values.username,
      password: values.password,
    };
    getLogin(data).then((res) => {
      // console.log(res);
      if (res.result !== 'fail') {
        message.success('登录成功！');
        const storage = window.localStorage;
        storage.token = res.message.token;
        this.props.history.push('/home');
      } else {
        message.error('登录失败！');
      }
    });
  }

  getSixty() {
    if (!this.state.isSendMess) {
      let { countDown } = this.state;
      const timer = setInterval(() => {
        this.setState(
          {
            countDown: (countDown -= 1),
            isSendMess: true,
            colStyle1: 13,
            colStyle2: 11,
          },
          () => {
            if (this.state.countDown === 0) {
              clearInterval(timer);
              this.setState({
                colStyle1: 16,
                colStyle2: 8,
                isSendMess: false,
                countDown: 60,
              });
            }
          },
        );
      }, 1000);
    }
  }

  handleVisibleChange(e) {
    this.setState({ visible: e });
  }

  handleMenuClick() {
    this.setState({ visible: false });
  }

  getChangeType(index) {
    this.setState({
      nowIndex: index,
    });
  }

  chooseAliPay() {
    this.setState({
      aliPay: !this.state.aliPay,
    });
  }

  // 底部登录方式
  chooseTaoBao() {
    this.setState({
      taobao: !this.state.taobao,
    });
  }

  chooseWeiBo() {
    this.setState({
      weibo: !this.state.weibo,
    });
  }
}

Login.propTypes = {
  history: PropTypes.object.isRequired,
};
